import _get from 'lodash.get';
import { TableRow, TableCell } from '@material-ui/core';
import _isEmpty from 'lodash.isempty';

type TextRowProps<T> = {
	data: T;
	source?: string;
	label: string;
	getValue?: (d: T) => string;
	defaultValue?: string;
	isHiddenEmptyValue?: boolean;
};

function TextRow<T>({
	data,
	source = '',
	label,
	getValue,
	defaultValue,
	isHiddenEmptyValue,
}: TextRowProps<T>) {
	const value = getValue
		? getValue(data)
		: _get(data, source) || defaultValue || '';

	if (isHiddenEmptyValue && _isEmpty(value)) return <></>;

	return (
		<TableRow>
			<TableCell variant='head' sx={{ width: { sm: 220 }, padding: 0 }}>
				{label}
			</TableCell>
			<TableCell>{value}</TableCell>
		</TableRow>
	);
}

export default TextRow;
